<script>
export default {
    name:'inputComponent',
    props:{
        text:{
            type:String,
            
        }
    },
    computed:{
        inputText:{
            get(){
                console.log('get',this.text);
                return this.text
            },
            set(val){
                console.log('set',val);
                // this.$emit('textChange',val)
                  this.$emit('update:text',val)
            }
        }
    }
}
</script>

<template src='./template.html'></template>

<style scoped src='./style.css'></style>